import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { SharedModule } from '@checkcars/shared';
import { AdvertisementModule } from '@checkcars/widgets';
import { NguCarouselModule } from '@ngu/carousel';
import { NgxGalleryModule } from '@checkcars/widgets';
import { VehicleOffersComponent } from './components/vehicle-offers/vehicle-offers.component';
import { WeBuyUsedCarsComponent } from './components/we-buy-used-cars/we-buy-used-cars.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgxCaptchaModule } from 'ngx-captcha';

const routes: Routes = [
  { path: 'vehicle-offers', component: VehicleOffersComponent},
  { path: 'we-buy-used-cars', component: WeBuyUsedCarsComponent},
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    HttpClientModule,
    SharedModule,
    AdvertisementModule,
    NguCarouselModule,
    NgxGalleryModule,
    FormsModule,
    NgxCaptchaModule,
    ReactiveFormsModule,
  ],
  declarations: [VehicleOffersComponent, WeBuyUsedCarsComponent],
  exports: [VehicleOffersComponent, WeBuyUsedCarsComponent],
})
export class NewVehicleModule {}
