import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ContactService } from '@checkcars/shared';
import { environment } from '@checkcars/environment/environment';

@Component({
  selector: 'checkcars-we-buy-used-cars',
  templateUrl: './we-buy-used-cars.component.html',
  styleUrls: ['./we-buy-used-cars.component.scss']
})
export class WeBuyUsedCarsComponent implements OnInit {
  contactForm;
  response: 'success' | 'error' | null = null;
  siteKey: string;
  constructor(
    private fb: FormBuilder,
    private contactService: ContactService
  ) {}

  ngOnInit(): void {
    this.initContactForm();
  }
  initContactForm() {
    this.siteKey = environment.recaptchaKey;
    this.contactForm = this.fb.group({
      Type: ['contact', Validators.required],
      Name: [null, Validators.required],
      Phone: [null, Validators.required],
      Email: [null, [Validators.required, Validators.email]],
      TermsAccepted: [false, [Validators.required, CustomValidators.accepted]],
      recaptcha: [false, [Validators.required, CustomValidators.accepted]],
      Subject: [null, Validators.required],
      Message: [null, Validators.required],
    });
  }
  submitContactForm() {
    this.contactService.sendBuyUsedVehicleRequest(this.contactForm.value).subscribe(
      () => {
        this.contactForm.reset();
        this.showFeedback('success');
      },
      () => {
        this.showFeedback('error');
      }
    );
  }
  showFeedback(status: 'success' | 'error'): void {
    this.response = status;
    setTimeout(() => {
      this.response = null;
    }, 3000);
  }
}
//Todo move to external file
import { AbstractControl } from '@angular/forms';

export class CustomValidators {
  public static accepted(control: AbstractControl) {
    return !control.value ? { accepted: true } : null;
  }
}
